body {
  margin: 0
}

.logo {
  margin: 25px 0
}

.search {
  display: flex;
  margin: 0 auto
}

.search__input {
  width: 230px;
  height: 30px;
  border: 1px solid rgba(0, 0, 0, .1);
  border-bottom-left-radius: 4px;
  border-top-left-radius: 4px;
  padding: 0 15px
}

.search__input:focus {
  outline: 0
}

.search button {
  display: flex;
  align-items: center;
  width: 30px;
  height: 30px;
  background: #dc3e2a;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer
}

.search button:focus {
  outline: 0
}

.nav {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer
}

.nav__item {
  position: relative
}

.nav__title {
  margin-left: 10px
}

.nav__badge {
  position: absolute;
  top: 2px;
  right: -5px;
  min-width: 16px;
  height: 10px;
  padding: 0 3px;
  text-align: center;
  background: #dc3e2a;
  border-radius: 8px;
  font-size: 10px;
  line-height: 11px;
  color: #fff
}

.breadcrumbs {
  color: rgba(0, 0, 0, .3)
}

.breadcrumbs a {
  color: rgba(0, 0, 0, .3)
}

.goods {
  margin-top: 25px
}

.goods__wrapper {
  display: flex;
  flex-wrap: wrap
}

.goods__item {
  position: relative;
  width: 240px;
  padding: 10px;
  box-shadow: 4px 4px 30px rgba(0, 0, 0, .2);
  border-radius: 4px;
  margin-bottom: 40px;
  margin-right: 35px;
  transition: .3s all;
}

.goods__item:hover {
  box-shadow: 4px 4px 40px rgba(0, 0, 0, .3)
}

.goods__item-remove {
  position: absolute;
  top: -10px;
  right: -10px;
  width: 25px;
  height: 25px;
  text-align: center;
  line-height: 22px;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 100%;
  background-color: #fff;
  cursor: pointer
}

.goods__item-remove:hover {
  color: #dc3e2a
}

.goods__img {
  width: 220px;
  -o-object-fit: cover;
  object-fit: cover
}

.goods__colors {
  font-size: 12px;
  text-align: center;
  margin-top: 5px;
  color: #003267
}

.goods__title {
  color: #003267;
  margin-top: 5px
}

.goods__price {
  text-align: center;
  color: #dc3e2a;
  margin-top: 5px;
  font-weight: 700
}

.goods__btn {
  width: 100%;
  height: 40px;
  background-color: #dc3e2a;
  border: none;
  outline: 0;
  margin-top: 20px;
  color: #fff;
  font-weight: 700;
  cursor: pointer;
}

.goods__btn:focus {
  outline: 0
}

.goods__btn:hover {
  box-shadow: 0 0 15px rgba(220, 62, 42, .7)
}

.cart {
  display: none;
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .3)
}

.cart__body {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 1024px;
  min-height: 200px;
  max-height: 640px;
  background-color: #fff;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
  padding: 25px;
  overflow: auto
}

.cart__title {
  font-size: 28px;
  color: rgba(0, 0, 0, .4)
}

.cart__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-top: 25px
}

.cart__wrapper .empty {
  font-size: 24px;
  color: rgba(0, 0, 0, .4)
}

.cart__wrapper .goods__item {
  margin-right: 75px
}

.cart__close {
  position: absolute;
  top: 25px;
  right: 25px;
  cursor: pointer;
}

.cart__total {
  font-size: 18px
}

.cart__total span {
  color: #dc3e2a
}

.cart__confirm {
  width: 280px;
  height: 40px;
  display: block;
  margin: 0 auto;
  background-color: #dc3e2a;
  border: none;
  outline: 0;
  margin-top: 20px;
  color: #fff;
  font-weight: 700;
  cursor: pointer
}

.cart__confirm:focus {
  outline: 0
}

.cart__confirm:hover {
  box-shadow: 0 0 15px rgba(220, 62, 42, .7)
}

.confirm {
  display: none;
  position: fixed;
  bottom: 50px;
  right: 50px;
  width: 100px;
  height: 100px
}

.confirm img {
  width: 100%
}